<template>
	<view>
		<!-- <h1>实名认证</h1> -->
			<view class="row b-b">
				<text class="tit">姓名</text>
				<input class="input" type="text" v-model="addressData.name" placeholder="请输入姓名" placeholder-class="placeholder" />
			</view>
			<view class="row b-b">
				<text class="tit">身份证号</text>
				<input class="input" type="text" v-model="addressData.id" placeholder="请输入身份证号" placeholder-class="placeholder" />
			</view>
			<!-- <view class="row b-b">
				<text class="tit">上传身份证</text>
			</view>
				<view class="add" >
					<image src="../../static/addid_icon.svg"></image>
					<text style="text-align: center;display: block;">添加身份证正面照</text>
				</view>
				<view class="add" >
					<image src="../../static/addid_icon.svg" ></image>
					<text style="text-align: center;display: block;">添加身份证反面照</text>
				</view>
 -->
		 <view class="grace-idcard-main">
			<view class="grace-idcard-text">
				身份证照片 ( 正面 )
			</view>
			<view class="grace-idcard-items">
				<view class="grace-idcard-uper-btn" @tap="selectImg1">
					<view class="img"><image src="../../static/camera.png" mode="widthFix" /></view>
					<view class="text">拍摄或选择照片</view>
				</view>
				<view class="grace-idcard-preview">
					<image :src="idCard1"  @tap="previewImg1" mode="widthFix"></image>
				</view>
			</view>
			<view class="grace-idcard-text">
				身份证照片 ( 背面 )
			</view>
			<view class="grace-idcard-items">
				<view class="grace-idcard-uper-btn" @tap="selectImg2">
					<view class="img"><image src="../../static/camera.png" mode="widthFix" /></view>
					<view class="text">拍摄或选择照片</view>
				</view>
				<view class="grace-idcard-preview">
					<image :src="idCard2" @tap="previewImg2" mode="widthFix" />
				</view>
			</view>
			
		 </view>
					<button class="add-btn" @click="confirm">下一步</button>
			</view>
</template>

<script>
	export default {
		data() {
			return {
				addressData:{
				 name: '',
				 id: ''
				},
				idCard1 : '../../static/idcard1.png',
				idCard2 : '../../static/idcard2.png'
			}
		},
		onLoad:function(){
		 
		},
		methods: {
					 confirm() {
									let data = this.addressData;
									console.log(JSON.stringify(data))
									uni.navigateTo({
										url:'../faceRecognition/faceRecognition'
									})
						},
						selectImg1 : function() {
							uni.chooseImage({
								count:1,
								success:function(res){
									this.idCard1 = res.tempFilePaths[0];
								}
							})
						},
						selectImg2 : function() {
							uni.chooseImage({
								count:1,
								success:function(res){
									this.idCard2 = res.tempFilePaths[0];
								}
							})
						},
						previewImg1 : function(){
							uni.previewImage({
								urls:[this.idCard1]
							});
						},
						previewImg2 : function(){
							uni.previewImage({
								urls:[this.idCard2]
							});
						},
						uploadCards : function(){
							if(this.idCard1 == '../../static/idcard1.png' || this.idCard2 == '../../static/idcard2.png'){
								uni.showToast({title:"请选择身份证照片", icon:"none"});
								return;
							}
							// 因底层限制一次上传一个
							uni.showLoading({title:"上传中"});
							// 上传正面
							var uploadTask1 = uni.uploadFile({
								url: 'https://unidemo.dcloud.net.cn/upload',
								filePath: this.idCard1,
								fileType: 'image',
								name: 'data',
								success: function (uploadFileRes) {
									// 上传成功后返回服务器端保存的路径
									console.log(uploadFileRes.data);
									// 上传背面
									var uploadTask2 = uni.uploadFile({
										url: 'https://unidemo.dcloud.net.cn/upload',
										filePath: this.idCard2,
										fileType: 'image',
										name: 'data',
										success: function (uploadFileRes) {
											// 上传成功后返回服务器端保存的路径
											console.log(uploadFileRes.data);
											// 至此2张照片上传完毕
											uni.hideLoading();
										}
									 });
								}
							 });
						}
		}
	}
</script>

<style scoped lang="scss">
	.add{
		width: 630rpx;height: 332rpx; background-color: red;margin-bottom: 20rpx;margin-left: 40rpx;
		overflow: hidden;
		image{
			width: 50rpx;
			height: 50rpx;
			margin: 20px auto;
			display: block;
			margin-top: 108rpx;
		}
	}
	 page {
	        padding-top: 16upx;
	    }
	
	    .row {
	        position: relative;
	        padding: 0 30upx;
			margin: 10px 0;
	        height: 110upx;
	        background: #fff;
	
	        .tit {
	            flex-shrink: 0;
	            width: 120upx;
	            font-size: 30rpx;
	
	        }
	
	        .input {
	            flex: 1;
	            font-size: 30upx;
				margin: 10px 0;
	        }
	
	        .icon-shouhuodizhi {
	            font-size: 36upx;
	
	        }
	    }
	
	    .add-btn {
	        display: flex;
	        align-items: center;
	        justify-content: center;
	        width: 690upx;
	        height: 80upx;
	        margin: 60upx auto;
	        background-color: #46A8FF;
	        color: #fff;
	        border-radius: 10upx;
	        box-shadow: 1px 2px 5px rgba(28, 42, 134, 0.4);
	    }
		view{font-size:28upx;}
		.grace-idcard-main{margin:20upx 30upx;}
		.grace-idcard-desc{line-height:2em; background:#FFFFFF; padding:20upx; border-radius:10upx;}
		.grace-idcard-text{line-height:2em; margin-top:30upx;}
		.grace-idcard-items{background:#FFFFFF; padding:30upx 0; display:flex; margin:30upx 0; border-radius:10upx; align-items: flex-start;}
		.grace-idcard-uper-btn{width:276upx; margin:0 60upx; background:#F1F1F1; padding-bottom:10upx; border-radius:10upx; text-align:center;}
		.grace-idcard-uper-btn .img{width:100upx; height:100upx; margin:0 auto; margin-top:30upx;}
		.grace-idcard-uper-btn .img image{width:100upx; height:100upx;}
		.grace-idcard-uper-btn .text{width:100%; margin-top:10upx; text-align:center; line-height:2em;}
		.grace-idcard-preview{width:50%; margin:0 30upx;}
		.grace-idcard-preview image{width:100%;}
</style>
